.quotes {
    padding-top: 109px;
    padding-bottom: 125px;
    background: linear-gradient(180deg, #EAF0FF 0%, #F6F9FF 100%);


    &__subtitle {
        @include text (10px, 500, 12px,#3D4F9F);
        letter-spacing: 0.1em;
        text-transform: uppercase;
        margin-bottom: 30px;
        font-family: 'Roboto';
    }

    &__title {
    @include text (36px, 500, 45px,#3D4F9F);
    font-family: 'Quicksand';
    margin-bottom: 15px;
    }

    &__text {
    @include text (12px, 600, 23px, #A4ABC8);  
    letter-spacing: 0.03em;
    font-family: 'Open Sans';
    max-width: 323px;
    }

    &__card {
        background: #FFFFFF;
        border: 1px solid #D8E3FE;
        box-sizing: border-box;
        box-shadow: 0px 5px 50px #F3F5FA;
        padding: 35px 55px 42px 67px;
        position: relative;
    }

    &__comment {
        max-width: 283px;
        @include text (14px, 400, 24px, #A4ABC8);
        font-family: 'Roboto'; 
    }

    &__client {
        width: 62px;
        border-radius: 50%;
        position: absolute;
        bottom: -33px;
        left: 180px;
        filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
        z-index: 1;
}

    &__inner {
        margin-right: 35px;
    }

    &__indicators li {
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background-color: #D1D6E3;
        margin-right: 8px;
        margin-left: 8px;
    }
    &__indicators {
        position: absolute;
        bottom: -46px;
        left: 20px;
    }
    &__carousel-inner {
        height: 245px;
    }

}